.App {
  text-align: center;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.Customer-image{
  border-radius: 6px;
  max-width: 90%;
  max-height: 275px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
  background-color: white;
  -webkit-box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.19);
}

.rotate{
  height: 5%;
  -webkit-transform: scaleY(-1);
  -ms-transform: scaleY(-1);
      transform: scaleY(-1);
  -webkit-filter: FlipV;
          filter: FlipV;
  -ms-filter: "FlipV";
}

.buttony {
  background:transparent;
  border-radius: 10px;
  border: none;
}

.buttony:focus {outline:0;}

